body {
  background-color: #d5d5d5;
  width: 100vw;
  height: 100vh;
}

.calculatorBox {
  width: calc(100vw - 69vw);
  height: calc(100vh - 25vh);
  margin: 7% auto 0 auto;
  padding: 0;
}

.inputsContainer {
  width: 100%;
  height: 15%;
  margin: 0;
  border: 0;
  padding: 0;
}

.inputBoxOpe {
  width: 10%;
  height: 100%;
  background-color: rgb(144, 147, 150);
  font-size: calc(100vw - 97vw);
  text-align: center;
  border: 0;
  margin: 0;
  padding: 0;
}

.inputBoxRe {
  width: 30%;
  height: 100%;
  background-color: rgb(144, 147, 150);
  font-size: calc(100vw - 97vw);
  text-align: center;
  border: 0;
  margin: 0;
  padding: 0;
}

.inputBoxNum {
  width: 30%;
  height: 100%;
  background-color: rgb(144, 147, 150);
  font-size: calc(100vw - 97vw);
  text-align: center;
  border: 0;
  margin: 0;
  padding: 0
}

.numbersBox {
  width: 100%;
  height: 85%;
  margin: 0;
  border: 0;
  border-top: 2px solid black;
  border-left: 2px solid black;
  background-color: rgb(182, 185, 187);
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  box-sizing: border-box;
}

.numberBox {
  width: calc(100% / 4);
    height: calc(100% / 4);
    background: radial-gradient(rgb(214, 210, 210), gray);
    box-sizing: border-box;
    font-size: calc(100vw - 97vw);
    text-align: center;
    cursor: pointer;
    border: 0;
    border-right: 2px solid black;
    border-bottom: 2px solid black
}

.operatorBox {
  width: calc(100% / 4);
    height: calc(100% / 4);
    background: radial-gradient(rgb(214, 210, 210), gray);
    box-sizing: border-box;
    text-align: center;
    font-size: calc(100vw - 97vw);
    cursor: pointer;
    border: 0;
    border-right: 2px solid black;
    border-bottom: 2px solid black
}

@media (max-width: 700px) {
  body {
    margin: 0;
    padding: 0;
  }
  .calculatorBox {
    margin: 0;
    width: 100vw;
    height: 100vh;
  }
  .inputsContainer {
    height: 40%;
  }
  .numbersBox  {
    height: 60%;
  }
  .operatorBox {
    font-size: calc(100vw - 93vw);
  }
  .numberBox {
    font-size: calc(100vw - 93vw);
  }
  .inputBoxNum {
    font-size: calc(100vw - 93vw);
  }
  .inputBoxRe {
    font-size: calc(100vw - 93vw);
  }
  .inputBoxOpe {
    font-size: calc(100vw - 93vw);
  }
}